import axios from "axios"; 
import React, { useEffect, useState } from "react";

export default function App() {
  const [income1, setIncome1] = useState([]);
  const [income2, setIncome2] = useState([]);
  const [loan, setLoan] = useState([]);
  const [credit_card, setCreditcard] = useState([]);
  const [total_income, setIncome] = useState([]);
   
  const [total_liabilities, setLiabilities] = useState([]);
  const [deposit, setDeposit] = useState([]);


  const [borrowingResult, setBorrowingResult] = useState(null);
  const [propertyResult, setPropertyResult] = useState(null); 
  
  const onSubmit = async (e) => {
    setIncome(parseInt(income1)+parseInt(income2))
    setLiabilities(parseInt(loan)+parseInt(credit_card))
    const body = { "total_income": parseInt(total_income),
    "total_liabilities": parseInt(total_liabilities),
    "deposit": parseInt(deposit)};
    try {
      const res = await axios.post('https://test-api-self.vercel.app/calculate', body, { "Access-Control-Allow-Origins": true} ) 
      res.headers( "Access-Control-Allow-Origin", "*") 
      setBorrowingResult(res.data.result.borrowing)
      setPropertyResult(res.data.result.property)    
      console.log(res.data)
      
    } catch (e) {
      alert(e)
    }
  } 

  return (
    <div className="w-full max-w-xs m-10">
      <form className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
        <h1 className="text-center mb-4 text-xl"> Calculator</h1>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Income 1
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            id="Income1 "
            type="integer"
            placeholder="Income"
            value={ income1 }
            onChange={(e) => setIncome1(e.target.value)}
          />
        </div>

        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Income 2
          </label>
          <input
            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            id="Income2 "
            type="integer"
            placeholder="Income"
            value={ income2 }
            onChange={(e) => setIncome2(e.target.value)}
          />
        </div>

        <div className="mb-6">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="password"
          >
            Loans
          </label>
          <input
            className="shadow appearance-none  rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="loans"
            type="integer"
            placeholder="Loans in $"
            value={loan}
            onChange={(e) => setLoan(e.target.value)}
          />
        </div>

        <div className="mb-6">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="password"
          >
            Credit Cards
          </label>
          <input
            className="shadow appearance-none  rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="creditCard"
            type="integer"
            placeholder="Credit Card in $"
            value={ credit_card }
            onChange={(e) => setCreditcard(e.target.value)}
          />
        </div>

        <div className="mb-6">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="password"
          >
            Deposit
          </label>
          <input
            className="shadow appearance-none  rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Deposit"
            type="integer"
            placeholder="Deposit in $"
            value={deposit}
            onChange={(e) => setDeposit(e.target.value)}
          />
        </div>
        <div className="flex items-center justify-center">
          <button
            className="bg-purple-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="button"
            onClick={onSubmit}
          >
            Calculate 
          </button>
        </div>
         
          <div className="mt-4">
            <p>Your borrowing is: {borrowingResult} </p>
            <p>Your property: {propertyResult}</p>
          </div>
         
      </form>
    </div>
  );
}
